import { getDaysInMonth } from 'date-fns';
import React from 'react';
import '../../Styles/calendar.scss';

const CALENDAR_LENGTH = 35; //    
const DEFAULT_TRASH_VALUE = 0; //    
const DAY_OF_WEEK = 7; //  
const DAY_LIST = ['', '', '', '', '', '', ''];

const Calendar: React.FC = () => {
  //  2025 1 1 
  const [currentDate] = React.useState(new Date(2025, 0, 1));
  const totalMonthDays = getDaysInMonth(currentDate);

  //      
  const prevDayList = Array.from({
    length: currentDate.getDay(), // 1     
  }).map(() => DEFAULT_TRASH_VALUE);

  //      
  const currentDayList = Array.from({ length: totalMonthDays }).map(
    (_, i) => i + 1,
  );

  //      
  const nextDayList = Array.from({
    length: CALENDAR_LENGTH - currentDayList.length - prevDayList.length,
  }).map(() => DEFAULT_TRASH_VALUE);

  //      
  const currentCalendarList = prevDayList.concat(currentDayList, nextDayList);
  
  //   
  const weekCalendarList = currentCalendarList.reduce(
    (acc: number[][], cur, idx) => {
      const chunkIndex = Math.floor(idx / DAY_OF_WEEK);
      if (!acc[chunkIndex]) {
        acc[chunkIndex] = [];
      }
      acc[chunkIndex].push(cur);
      return acc;
    },
    [],
  );

  return (
    <div className='calendar'>
      <h2 style={{ margin: '20px 0' }}>2025 1</h2>
      <div>
        {DAY_LIST.map((day) => (
          <span key={day} 
            style={{ 
              width: '44px', 
              height: '40px', 
              display: 'inline-block', 
              textAlign: 'center',
              color: day === '' ? '#bd3232' : '#2e2e2e',
            }}>
            {day}
          </span>
        ))}
      </div>
      {weekCalendarList.map((week, weekIndex) => (
        <div key={weekIndex} style={{ display: 'flex' }}>
          {week.map((day, dayIndex) => {
            //  
            let textColor = '#2e2e2e';
            let backgroundColor = 'transparent';

            if (day === 4) {
              textColor = '#fff'; // 4
              backgroundColor = '#9b819b'; // 4 
            } else if ([5, 12, 19, 26].includes(day)) {
              textColor = '#bd3232'; // 5, 12, 19, 26  
            }

            return (
              <span
                key={dayIndex}
                style={{
                  width: '40px',
                  height: '40px',
                  display: 'inline-flex', // Flexbox 
                  alignItems: 'center', //   
                  justifyContent: 'center', //   
                  backgroundColor: backgroundColor,
                  borderRadius: '35px',
                  padding: '6px 0',
                  color: textColor,
                  margin: '2px', //  
                }}
              >
                {day === DEFAULT_TRASH_VALUE ? '' : day}
              </span>
            );
          })}
        </div>
      ))}
    </div>
  );
};

export default Calendar;
